.pipeline-ui {
  display: flex;
  flex-direction: column;
  height: 100%; }

.pipeline-ui__view {
  list-style: none;
  padding: 12px 24px 24px;
  margin: 0; }
  .pipeline-ui__view .kui-switch {
    margin-bottom: 20px; }
  .pipeline-ui__view > li:last-child .kui-switch {
    margin-bottom: 0; }

.pipeline-ui__tag-list {
  margin: 0;
  padding: 0;
  list-style: none; }
  .pipeline-ui__tag-list .pipeline-ui__tag-list-item {
    padding: 0; }
    .pipeline-ui__tag-list .pipeline-ui__tag-list-item label {
      padding: 10px 12px; }

.pipeline-ui .kui-toggle {
  width: 230px;
  flex: none;
  padding: 12px 24px; }

.pipeline-ui .kui-dropdown {
  margin: 12px; }
  .pipeline-ui .kui-dropdown .kui-switch {
    margin: 0;
    width: 100%; }
    .pipeline-ui .kui-dropdown .kui-switch label {
      max-width: 100%; }
      .pipeline-ui .kui-dropdown .kui-switch label span {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: calc(100% - 35px); }
