.pipeline-tags .kui-dropdown {
  display: block;
  margin: 12px; }
  .pipeline-tags .kui-dropdown .kui-switch {
    margin: 0;
    width: 100%; }
    .pipeline-tags .kui-dropdown .kui-switch label {
      max-width: 100%; }
      .pipeline-tags .kui-dropdown .kui-switch label span {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: calc(100% - 35px); }

.pipeline-tags__tag-list {
  margin: 0;
  padding: 0;
  list-style: none; }
  .pipeline-tags__tag-list .pipeline-tags__tag-list-item {
    padding: 0; }
    .pipeline-tags__tag-list .pipeline-tags__tag-list-item label {
      padding: 10px 12px; }

.pipeline-tags__empty {
  font-size: 1.4em; }
